/* vdl-color using default palette 'vdl-palette-1'
  Examples of use:
  vdl-color(15);

  to specify a different color palette:
  vdl-color(4, $some-other-palette);
 */
@keyframes vdl-checkbox-fade-in-background {
  0% {
    opacity: 0; }
  50% {
    opacity: 1; } }

@keyframes vdl-checkbox-fade-out-background {
  0%, 50% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes vdl-checkbox-unchecked-checked-checkmark-path {
  0%, 50% {
    stroke-dashoffset: 22.91026; }
  50% {
    animation-timing-function: cubic-bezier(0, 0, 0.2, 0.1); }
  100% {
    stroke-dashoffset: 0; } }

@keyframes vdl-checkbox-unchecked-indeterminate-mixedmark {
  0%, 68.2% {
    transform: scaleX(0); }
  68.2% {
    animation-timing-function: cubic-bezier(0, 0, 0, 1); }
  100% {
    transform: scaleX(1); } }

@keyframes vdl-checkbox-checked-unchecked-checkmark-path {
  from {
    animation-timing-function: cubic-bezier(0.4, 0, 1, 1);
    stroke-dashoffset: 0; }
  to {
    stroke-dashoffset: -22.91026; } }

@keyframes vdl-checkbox-checked-indeterminate-checkmark {
  from {
    animation-timing-function: cubic-bezier(0, 0, 0.2, 0.1);
    opacity: 1;
    transform: rotate(0deg); }
  to {
    opacity: 0;
    transform: rotate(45deg); } }

@keyframes vdl-checkbox-indeterminate-checked-checkmark {
  from {
    animation-timing-function: cubic-bezier(0.14, 0, 0, 1);
    opacity: 0;
    transform: rotate(45deg); }
  to {
    opacity: 1;
    transform: rotate(360deg); } }

@keyframes vdl-checkbox-checked-indeterminate-mixedmark {
  from {
    animation-timing-function: cubic-bezier(0, 0, 0.2, 0.1);
    opacity: 0;
    transform: rotate(-45deg); }
  to {
    opacity: 1;
    transform: rotate(0deg); } }

@keyframes vdl-checkbox-indeterminate-checked-mixedmark {
  from {
    animation-timing-function: cubic-bezier(0.14, 0, 0, 1);
    opacity: 1;
    transform: rotate(0deg); }
  to {
    opacity: 0;
    transform: rotate(315deg); } }

@keyframes vdl-checkbox-indeterminate-unchecked-mixedmark {
  0% {
    animation-timing-function: linear;
    opacity: 1;
    transform: scaleX(1); }
  32.8%, 100% {
    opacity: 0;
    transform: scaleX(0); } }

.vdl-checkbox-frame, .vdl-checkbox-background, .vdl-checkbox-checkmark {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0; }

.vdl-checkbox-checkmark, .vdl-checkbox-mixedmark {
  width: calc(100% - 4px); }

.vdl-checkbox-frame, .vdl-checkbox-background {
  border-radius: 3px;
  box-sizing: border-box;
  pointer-events: none; }

vdl-checkbox {
  cursor: pointer;
  padding: 0;
  padding-bottom: 13px;
  display: inline-block; }

.vdl-checkbox-layout {
  cursor: inherit;
  align-items: baseline;
  vertical-align: middle;
  display: inline-flex; }

.vdl-checkbox-inner-container {
  display: inline-block;
  height: 20px;
  line-height: 0;
  margin: 2px 8px auto auto;
  order: 0;
  position: relative;
  vertical-align: middle;
  white-space: nowrap;
  width: 20px;
  flex-shrink: 0; }
  [dir='rtl'] .vdl-checkbox-inner-container {
    margin-left: 8px;
    margin-right: auto; }

.vdl-checkbox-layout .vdl-checkbox-label-container {
  line-height: 24px; }

.vdl-checkbox-frame {
  background-color: transparent;
  border: 2px solid;
  transition: border-color 90ms cubic-bezier(0, 0, 0.2, 0.1); }

.vdl-checkbox-background {
  align-items: center;
  display: inline-flex;
  justify-content: center;
  transition: background-color 90ms cubic-bezier(0, 0, 0.2, 0.1), opacity 90ms cubic-bezier(0, 0, 0.2, 0.1); }

.vdl-checkbox-checkmark {
  width: 100%; }

.vdl-checkbox-checkmark-path {
  stroke-dashoffset: 22.91026;
  stroke-dasharray: 22.91026;
  stroke-width: 2.66667px; }

.vdl-checkbox-mixedmark {
  height: 2px;
  opacity: 0;
  transform: scaleX(0) rotate(0deg); }

.vdl-checkbox-label-before .vdl-checkbox-inner-container {
  order: 1;
  margin-left: 8px;
  margin-right: auto; }
  [dir='rtl'] .vdl-checkbox-label-before .vdl-checkbox-inner-container {
    margin-left: auto;
    margin-right: 8px; }

.vdl-checkbox-label-before .vdl-checkbox-label-container {
  text-align: right; }

.vdl-checkbox-anim-unchecked-indeterminate .vdl-checkbox-background {
  background-color: #005a99; }

.vdl-checkbox-checked .vdl-checkbox-background {
  background-color: #005a99; }

.vdl-checkbox-checked .vdl-checkbox-checkmark {
  opacity: 1; }

.vdl-checkbox-checked .vdl-checkbox-checkmark-path {
  stroke-dashoffset: 0; }

.vdl-checkbox-checked .vdl-checkbox-mixedmark {
  transform: scaleX(1) rotate(-45deg); }

.vdl-checkbox-indeterminate .vdl-checkbox-checkmark {
  opacity: 0;
  transform: rotate(45deg); }

.vdl-checkbox-indeterminate .vdl-checkbox-checkmark-path {
  stroke-dashoffset: 0; }

.vdl-checkbox-indeterminate .vdl-checkbox-mixedmark {
  opacity: 1;
  transform: scaleX(1) rotate(0deg); }

.vdl-checkbox-unchecked .vdl-checkbox-background {
  background-color: transparent; }

.vdl-checkbox-disabled {
  cursor: default; }

.vdl-checkbox-anim-unchecked-checked .vdl-checkbox-background {
  animation: 180ms linear 0ms vdl-checkbox-fade-in-background; }

.vdl-checkbox-anim-unchecked-checked .vdl-checkbox-checkmark-path {
  animation: 180ms linear 0ms vdl-checkbox-unchecked-checked-checkmark-path; }

.vdl-checkbox-anim-unchecked-indeterminate .vdl-checkbox-background {
  animation: 180ms linear 0ms vdl-checkbox-fade-in-background; }

.vdl-checkbox-anim-unchecked-indeterminate .vdl-checkbox-mixedmark {
  animation: 90ms linear 0ms vdl-checkbox-unchecked-indeterminate-mixedmark; }

.vdl-checkbox-anim-checked-unchecked .vdl-checkbox-background {
  animation: 180ms linear 0ms vdl-checkbox-fade-out-background; }

.vdl-checkbox-anim-checked-unchecked .vdl-checkbox-checkmark-path {
  animation: 90ms linear 0ms vdl-checkbox-checked-unchecked-checkmark-path; }

.vdl-checkbox-anim-checked-indeterminate .vdl-checkbox-checkmark {
  animation: 90ms linear 0ms vdl-checkbox-checked-indeterminate-checkmark; }

.vdl-checkbox-anim-checked-indeterminate .vdl-checkbox-mixedmark {
  animation: 90ms linear 0ms vdl-checkbox-checked-indeterminate-mixedmark; }

.vdl-checkbox-anim-indeterminate-checked .vdl-checkbox-checkmark {
  animation: 500ms linear 0ms vdl-checkbox-indeterminate-checked-checkmark; }

.vdl-checkbox-anim-indeterminate-checked .vdl-checkbox-mixedmark {
  animation: 500ms linear 0ms vdl-checkbox-indeterminate-checked-mixedmark; }

.vdl-checkbox-anim-indeterminate-unchecked .vdl-checkbox-background {
  animation: 180ms linear 0ms vdl-checkbox-fade-out-background; }

.vdl-checkbox-anim-indeterminate-unchecked .vdl-checkbox-mixedmark {
  animation: 300ms linear 0ms vdl-checkbox-indeterminate-unchecked-mixedmark; }

.vdl-checkbox-input {
  bottom: 0;
  left: 50%; }

.vdl-checkbox-frame {
  border-color: #414142; }

.vdl-checkbox-checkmark {
  fill: #fff; }

.vdl-checkbox-checkmark-path {
  stroke: #fff !important; }

.vdl-checkbox-mixedmark {
  background-color: #fff; }

[required] .vdl-checkbox-required {
  color: #b1181e; }

.vdl-hint-container {
  line-height: 0.75rem;
  font-size: 0.75rem; }

.vdl-checkbox-error .vdl-checkbox-frame {
  border-color: #b1181e; }

.vdl-checkbox-focused .vdl-checkbox-frame {
  border-width: 3px;
  margin: -1px; }

.vdl-checkbox-disabled.vdl-checkbox-checked .vdl-checkbox-background, .vdl-checkbox-disabled.vdl-checkbox-indeterminate .vdl-checkbox-background {
  background-color: #a5a5a5; }

.vdl-checkbox-disabled:not(.vdl-checkbox-checked) .vdl-checkbox-frame {
  border-color: #a5a5a5; }

.vdl-checkbox-disabled .vdl-checkbox-label-container,
.vdl-checkbox-disabled .vdl-hint-container .vdl-hint,
.vdl-checkbox-disabled .vdl-checkbox-required {
  color: #a5a5a5; }
